<script>
    import { defaultInput, defaultCompanyInfo } from "./stores/constants"

    function deleteLocalStorage() {
        localStorage.setItem("input_en", JSON.stringify($defaultInput))
		localStorage.setItem("firmenInfo_en", JSON.stringify($defaultCompanyInfo))
        console.log("yeeted and deleted")
        location.reload()
}

</script>

<p>Willkommen zur Demopage des Agile Maturity Tools!</p>

<p>Dies ist eine Demoversion des Tools, welche die Funktionalitäten veranschaulichen soll. Das Tool ist noch in Entwicklung, weswegen vor allem die visuellen Aspekte des Tools noch nicht ganz ausgereift sind.</p>

<p>
Das agile Reifegradmodell wurde im Competence Center 08 und 09 entwickelt. Ziel ist die Einschätzung des Reifegrads agiler Fähigkeiten, Ableitung des agilen Reifegrads des Unternehmens sowie Bereitstellung indikativer Handlungsempfehlungen zum Fähigkeitenaufbau. 
</p>

<p>Das Modell umfasst folgende Artefakte:</p>
<ul>
    <li>Der <b>Agile Maturity Grid</b> gibt eine übersicht über notwendige agile Fähigkeiten inklusive Beschreibung und einem Mapping auf die zugehörige Agility Dimension.</li>
    <li>Der Nutzen des <b>Schnell-Check</b> liegt in der schnellen Einschätzung des vorhandenen Reifegrads der entsprechenden agilen Fähigkeiten sowie Indikation über den agilen Reifegrad des Unternehmens.</li>
    <li>Das <b>Tool</b> dient zur Ergänzung der ersten beiden Artefakte und erlaubt es Nutzern indikative Handlungsempfehlungen zur Verbesserung der agilen Fähigkeiten und somit des agilen Reifegrads des Unternehmens </li>
</ul> 

<p>Alle drei Artefakte können auf verschiedene Unternehmensbereiche angewendet werden. Voraussetzung hierfür ist, das sich der betreffende Unternehmensbereich am Anfang oder mitten in einer agilen Transformation befindet. Das Modell sollte von einer repräsentativen Anzahl der Mitarbeitenden ausgefüllt werden. Hierbei sollte beachtet werden, dass auch unterschiedliche Fachbereiche und Hierarchiestufen berücksichtigt werden, um eine möglichst realistische Einschätzung zu der Ausprägung der aufgeführten agilen Fähigkeiten zu enthalten.</p>


